<template>
  <v-container
    id="regular-tables"
    fluid
    tag="section"
  >
    <base-v-component
      link="components/simple-tables"
      />
    <base-material-card
      icon="mdi-clipboard-text"
      title="Review"
      class="px-5 py-3"
      >
    <v-data-table
      :headers="headers"
      :items="reviews"
      sort-by="calories"
      class="elevation-1"
      >
    <template v-slot:top>
      <v-toolbar
        flat
        color="white"
        >
        <v-divider
          class="mx-4"
          inset
          vertical
        ></v-divider>
        <v-spacer></v-spacer>
        <v-dialog
            v-model="dialog"
            max-width="800px"
            >
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              color="primary"
              dark
              class="mb-2"
              v-bind="attrs"
              v-on="on"
              ><v-icon>mdi-pencil</v-icon></v-btn>
          </template>
          <v-card>
            <v-card-title>
              <span class="headline">{{ formTitle }}</span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <v-col
                    cols="12"
                    sm="2"
                    md="2"
                    >
                    <v-text-field
                      v-model="editedItem.srno"
                      label="SR No"
                      ></v-text-field>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="2"
                    md="2"
                    >
                    <v-text-field
                      v-model="editedItem.revno"
                      label="Rev No."
                      ></v-text-field>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="8"
                    md="8"
                    >
                    <v-text-field
                      v-model="editedItem.srtitle"
                      label="SR Name"
                      ></v-text-field>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col
                    cols="12"
                    sm="2"
                    md="2"
                    >
                    <v-select
                      :items="developer"
                      label=""
                      ></v-select>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="2"
                    md="2"
                    >
                    <v-select
                      :items="charger"
                      label=""
                      ></v-select>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="2"
                    md="2"
                    >
                    <v-menu
                        v-model="menu2"
                        :close-on-content-click="false"
                        :nudge-right="40"
                        transition="scale-transition"
                        offset-y
                        min-width="290px"
                        >
                        <template
                            v-slot:activator="{ on, attrs }"
                            >
                            <v-text-field
                              v-model="date"
                              label=""
                              readonly
                              v-bind="attrs"
                              v-on="on"
                            ></v-text-field>
                        </template>
                        <v-date-picker
                          v-model="date"
                          @input="menu2 = false"
                          ></v-date-picker>
                    </v-menu>
                    </v-col>
                    <v-col
                      cols="12"
                      sm="2"
                      md="2"
                      >
                      <v-select
                        :items="serves"
                        label=" "
                        multiple
                        persistent-hint
                        >
                      </v-select>
                    </v-col>
                    <v-col
                      cols="12"
                      sm="2"
                      md="2"
                      >
                      <v-select
                        :items="site"
                        label="Site"
                        multiple
                        persistent-hint
                        >
                      </v-select>
                    </v-col>
                </v-row>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                color="red darken-1"
                text
                @click="close"
                >Cancel</v-btn>
              <v-btn
                color="blue darken-1"
                text
                @click="save"
                >Save</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>
    <template v-slot:item.actions="{ item }">
      <v-icon
        small
        class="mr-2"
        @click="editItem(item)"
        >
        mdi-pencil
      </v-icon>
    </template>
    <template v-slot:no-data>
      <v-btn
        color="primary"
        @click="initialize"
        >Reset</v-btn>
    </template>
  </v-data-table>
    </base-material-card>
  </v-container>
</template>

<script>
  export default {
    data: () => ({
      date: new Date().toISOString().substr(0, 10),
      menu: false,
      modal: false,
      menu2: false,
      dialog: false,
      reviews: [],
      serves: ['AP', 'UI'],
      sites: ['', '', 'SESS', 'SCS'],
      developer: ['', '', ''],
      charger: ['', '', '', '', ''],
      headers: [
        {
          text: 'SR No.',
          align: 'start',
          sortable: false,
          value: 'srno',
        },
        { text: 'SR Title', value: 'srtitle' },
        { text: 'Rev No.', value: 'revno' },
        { text: '', value: 'developer' },
        { text: '', value: 'charger' },
        { text: '', value: 'date' },
        { text: '', value: 'servers' },
        { text: 'Site', value: 'charger' },
        { text: 'Actions', value: 'actions', sortable: false },
      ],
      desserts: [],
      editedIndex: -1,
      editedItem: {
        srno: '',
        srtitle: '',
        revno: '',
      },
      defaultItem: {
        srno: '',
        srtitle: '',
        revno: '',
      },
    }),

    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
    },

    created () {
      this.initialize()
    },

    methods: {
      initialize () {
        this.$http.get('/api/reviews').then((response) => { this.reviews = response.data })
      },

      editItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem (item) {
        const index = this.desserts.indexOf(item)
        confirm('Are you sure you want to delete this item?') && this.desserts.splice(index, 1)
      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      save () {
        if (this.editedIndex > -1) {
          Object.assign(this.desserts[this.editedIndex], this.editedItem)
        } else {
          this.desserts.push(this.editedItem)
        }
        this.close()
      },
    },
  }
</script>
